}
.mdc-top-app-bar{
    z-index: 2000;
}

.loader {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-items: center;
    text-align: center;
    background: rgba(0,0,0,.5);
}

.loader .loader__container {
    margin: 0 auto;
}
.map-wrapper {
    height: 100%;
    width: 100%;
    position: relative;
}
.dd-wrapper {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    position: relative;
    /* width: 222px; */
  }
  .dd-wrapper .dd-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    line-height: 38px;
    border: 1px solid #dfdfdf;
    border-radius: 3px;
    cursor: default;
    position: relative;
    background-color: #fff;
  }
  .dd-wrapper .dd-header span {
    margin-right: 20px;
  }
  .dd-wrapper .dd-header-title {
    font-weight: 300;
    margin: 2px 20px;
    margin-right: 30px;
    width: 100%;
  }
  .dd-wrapper .angle-down {
    color: #000;
    margin-right: 20px;
  }
  .dd-wrapper .dd-list {
    z-index: 10;
    position: absolute;
    width: 100%;
    border: 1px solid #dfdfdf;
    border-top: none;
    border-bottom-right-radius: 3px;
    border-bottom-left-radius: 3px;
    background-color: #fff;
    box-shadow: 0 2px 5px -1px #e8e8e8;
    font-weight: 700;
    padding: 15px 0;
    max-height: 215px;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
  }
  .dd-wrapper .dd-list-item {
    width: 100%;
    font-size: 1.5rem;
    padding: 8px 10px;
    line-height: 1.6rem;
    cursor: default;
    display: inline-block;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .dd-wrapper .dd-list-item.selected {
    color: #fff;
    background-color: #ffcc01;
  }
  .dd-wrapper .dd-list-item:hover {
    color: #fff;
    background-color: #ffcc01;
  }
.FourOhFour {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #121212;
  display: flex;
  align-items: center;
  justify-content: center;
}
.FourOhFour .bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-size: cover;
  mix-blend-mode: overlay;
}
.FourOhFour .code {
  font-family: "Alfa Slab One", cursive;
  font-size: 144px;
  height: 100vh;
  color: white;
  width: 100%;
  display: flex;
  background-position: center;
  align-items: center;
  background-size: cover;
  justify-content: center;
}
